	module.exports = {"data":{"postsJson":{"title":"Growth & Persecution","questionsTitle":"LEARNING FROM THE PAST","display":"block","bookName":"Acts","ref":"3:1-5:11","html":"<p>   <strong>3</strong><u>1</u>Peter and John were going up into the temple at the hour of prayer, the ninth hour.* <u>2</u>A certain man who was lame from his mother’s womb was being carried, whom they laid daily at the door of the temple which is called Beautiful, to ask gifts for the needy of those who entered into the temple. <u>3</u>Seeing Peter and John about to go into the temple, he asked to receive gifts for the needy. <u>4</u>Peter, fastening his eyes on him, with John, said,</p><h1>SIMON PETER</h1><h3>Look at us.</h3><p><u>5</u>He listened to them, expecting to receive something from them. <u>6</u>But Peter said,</p><h1>SIMON PETER</h1><h3>Silver and gold have I none, but what I have, that I give you. In the name of Jesus Christ of Nazareth, get up and walk!</h3><p><u>7</u>He took him by the right hand, and raised him up. Immediately his feet and his ankle bones received strength. <u>8</u>Leaping up, he stood, and began to walk. He entered with them into the temple, walking, leaping, and praising God. <u>9</u>All the people saw him walking and praising God. <u>10</u>They recognized him, that it was he who used to sit begging for gifts for the needy at the Beautiful Gate of the temple. They were filled with wonder and amazement at what had happened to him. <u>11</u>As the lame man who was healed held on to Peter and John, all the people ran together to them in the porch that is called Solomon’s, greatly wondering.<br />   <u>12</u>When Peter saw it, he responded to the people,</p><h1>SIMON PETER</h1><h3>You men of Israel, why do you marvel at this man? Why do you fasten your eyes on us, as though by our own power or godliness we had made him walk? <u>13</u>The God of Abraham, Isaac, and Jacob, the God of our fathers, has glorified his Servant Jesus, whom you delivered up, and denied in the presence of Pilate, when he had determined to release him. <u>14</u>But you denied the Holy and Righteous One, and asked for a murderer to be granted to you, <u>15</u>and killed the Prince of life, whom God raised from the dead, to which we are witnesses. <u>16</u>By faith in his name, his name has made this man strong, whom you see and know. Yes, the faith which is through him has given him this perfect soundness in the presence of you all.<br />   <u>17</u>Now, brothers,* I know that you did this in ignorance, as did also your rulers. <u>18</u>But the things which God announced by the mouth of all his prophets, that Christ should suffer, he thus fulfilled.<br />   <u>19</u>Repent therefore, and turn again, that your sins may be blotted out, so that there may come times of refreshing from the presence of the Lord, <u>20</u>and that he may send Christ Jesus, who was ordained for you before, <u>21</u>whom heaven must receive until the times of restoration of all things, which God spoke long ago by the mouth of his holy prophets. <u>22</u>For Moses indeed said to the fathers, ‘The Lord God will raise up a prophet for you from among your brothers, like me. You shall listen to him in all things whatever he says to you. <u>23</u>It will be that every soul that will not listen to that prophet will be utterly destroyed from among the people.’* <u>24</u>Yes, and all the prophets from Samuel and those who followed after, as many as have spoken, they also told of these days. <u>25</u>You are the children of the prophets, and of the covenant which God made with our fathers, saying to Abraham, ‘In your offspring* will all the families of the earth be blessed.’* <u>26</u>God, having raised up his servant Jesus, sent him to you first to bless you, in turning away every one of you from your wickedness.</h3><p>   <strong>4</strong><u>1</u>As they spoke to the people, the priests and the captain of the temple and the Sadducees came to them, <u>2</u>being upset because they taught the people and proclaimed in Jesus the resurrection from the dead. <u>3</u>They laid hands on them, and put them in custody until the next day, for it was now evening. <u>4</u>But many of those who heard the word believed, and the number of the men came to be about five thousand.<br />   <u>5</u>In the morning, their rulers, elders, and scribes were gathered together in Jerusalem. <u>6</u>Annas the high priest was there, with Caiaphas, John, Alexander, and as many as were relatives of the high priest. <u>7</u>When they had stood them in the middle of them, they inquired,</p><h1>JEWISH LEADERS</h1><h4>By what power, or in what name, have you done this?</h4><p>   <u>8</u>Then Peter, filled with the Holy Spirit, said to them,</p><h1>SIMON PETER</h1><h3>You rulers of the people, and elders of Israel, <u>9</u>if we are examined today concerning a good deed done to a crippled man, by what means this man has been healed, <u>10</u>be it known to you all, and to all the people of Israel, that in the name of Jesus Christ of Nazareth, whom you crucified, whom God raised from the dead, in him does this man stand here before you whole. <u>11</u>He is ‘the stone which was regarded as worthless by you, the builders, which has become the head of the corner.’* <u>12</u>There is salvation in none other, for neither is there any other name under heaven, that is given among men, by which we must be saved!</h3><p>   <u>13</u>Now when they saw the boldness of Peter and John, and had perceived that they were unlearned and ignorant men, they marveled. They recognized that they had been with Jesus. <u>14</u>Seeing the man who was healed standing with them, they could say nothing against it. <u>15</u>But when they had commanded them to go aside out of the council, they conferred among themselves, <u>16</u>saying,</p><h1>JEWISH LEADERS</h1><h4>What shall we do to these men? Because indeed a notable miracle has been done through them, as can be plainly seen by all who dwell in Jerusalem, and we can’t deny it. <u>17</u>But so that this spreads no further among the people, let’s threaten them, that from now on they don’t speak to anyone in this name.</h4><p><u>18</u>They called them, and commanded them not to speak at all nor teach in the name of Jesus.<br />   <u>19</u>But Peter and John answered them,</p><h1>SIMON PETER &amp; JOHN THE DISCIPLE</h1><h3>Whether it is right in the sight of God to listen to you rather than to God, judge for yourselves, <u>20</u>for we can’t help telling the things which we saw and heard.</h3><p>   <u>21</u>When they had further threatened them, they let them go, finding no way to punish them, because of the people; for everyone glorified God for that which was done. <u>22</u>For the man on whom this miracle of healing was performed was more than forty years old.<br />   <u>23</u>Being let go, they came to their own company, and reported all that the chief priests and the elders had said to them. <u>24</u>When they heard it, they lifted up their voice to God with one accord, and said,</p><h1>BELIEVERS IN JERUSALEM</h1><h4>O Lord, you are God, who made the heaven, the earth, the sea, and all that is in them; <u>25</u>who by the mouth of your servant, David, said,<br />   ‘Why do the nations rage,<br />   and the peoples plot a vain thing?<br />   <u>26</u>The kings of the earth take a stand,<br />   and the rulers take council together,<br />   against the Lord, and against his Christ.’**<br />   <u>27</u>For truly, in this city against your holy servant, Jesus, whom you anointed, both Herod and Pontius Pilate, with the Gentiles and the people of Israel, were gathered together <u>28</u>to do whatever your hand and your council foreordained to happen. <u>29</u>Now, Lord, look at their threats, and grant to your servants to speak your word with all boldness, <u>30</u>while you stretch out your hand to heal; and that signs and wonders may be done through the name of your holy Servant Jesus.</h4><p>   <u>31</u>When they had prayed, the place was shaken where they were gathered together. They were all filled with the Holy Spirit, and they spoke the word of God with boldness. <u>32</u>The multitude of those who believed were of one heart and soul. Not one of them claimed that anything of the things which he possessed was his own, but they had all things in common. <u>33</u>With great power, the apostles gave their testimony of the resurrection of the Lord Jesus. Great grace was on them all. <u>34</u>For neither was there among them any who lacked, for as many as were owners of lands or houses sold them, and brought the proceeds of the things that were sold, <u>35</u>and laid them at the apostles’ feet, and distribution was made to each, according as anyone had need. <u>36</u>Joses, who by the apostles was also called Barnabas (which is, being interpreted, Son of Encouragement), a Levite, a man of Cyprus by race, <u>37</u>having a field, sold it, and brought the money and laid it at the apostles’ feet.<br />   <strong>5</strong><u>1</u>But a certain man named Ananias, with Sapphira, his wife, sold a possession, <u>2</u>and kept back part of the price, his wife also being aware of it, and brought a certain part, and laid it at the apostles’ feet. <u>3</u>But Peter said,</p><h1>SIMON PETER</h1><h3>Ananias, why has Satan filled your heart to lie to the Holy Spirit, and to keep back part of the price of the land? <u>4</u>While you kept it, didn’t it remain your own? After it was sold, wasn’t it in your power? How is it that you have conceived this thing in your heart? You haven’t lied to men, but to God.</h3><p>   <u>5</u>Ananias, hearing these words, fell down and died. Great fear came on all who heard these things. <u>6</u>The young men arose and wrapped him up, and they carried him out and buried him. <u>7</u>About three hours later, his wife, not knowing what had happened, came in. <u>8</u>Peter answered her,</p><h1>SIMON PETER</h1><h3>Tell me whether you sold the land for so much.</h3><p>   She said,</p><h1>SAPPHIRA</h1><h4>Yes, for so much.</h4><p>   <u>9</u>But Peter asked her,</p><h1>SIMON PETER</h1><h3>How is it that you have agreed together to tempt the Spirit of the Lord? Behold, the feet of those who have buried your husband are at the door, and they will carry you out.</h3><p>   <u>10</u>She fell down immediately at his feet, and died. The young men came in and found her dead, and they carried her out and buried her by her husband. <u>11</u>Great fear came on the whole assembly, and on all who heard these things.</p><h5>*Acts 3:1 3:00 PM<br />*Acts 3:17 The word for “brothers” here may be also correctly translated “brothers and sisters” or “siblings.”<br />*Acts 3:23 Deuteronomy 18:15,18-19<br />*Acts 3:25 or, seed<br />*Acts 3:25 Genesis 22:18; 26:4<br />*Acts 4:11 Psalm 118:22<br />*Acts 4:26 Christ (Greek) and Messiah (Hebrew) both mean Anointed One.<br />*Acts 4:26 Psalm 2:1-2</h5>","path":"/353","Question1":"With which character do you most identify in this passage? Why?","Question2":"What strengths and/or weaknesses do you see in this character in this passage?","Question3":"What do you think God was wanting do in the life of your selected character?","Question4":"What do you think God is wanting to do in your life as you reflect on this passage? How can you cooperate with his loving purposes this week?","prevPath":"/352","nextPath":"/354"}},"pathContext":{}}

